<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page detail">
        <div class="page_title">
          <h2></h2>
          <p>2018-11-11 09:20:00</p>
        </div>
        <div class="content">
          <p>1220203141313135843</p>
          <p></p>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'page',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        }
      }
    }
  }
</script>
<style lang="less">
  .detail{
     color:#333;
     padding:20px 15px;
     .page_title{
        width:100%;
        padding-bottom:20px;
        h2{
           font-size:16px;
           padding-bottom:5px;
        }
        p{
           font-size:11px;
           color:#999999;
           text-align:center;
        }
     }
     .content{
        width:100%;
        padding-bottom:30px;
        p{
           text-align:left;
           text-indent:2em;
           line-height:1.5em;
           font-size:14px;
           color:#666666;
           padding:2px 0;
        }
     }
  }
</style>



