<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll"
      :options="options"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp"
      class="page_full bodyBg">
      <div class="msg_list">
        <div class="msg_box">
          <h2><span>2018-11-12</span></h2>
          <p class="all"></p>
          <i class="down"></i>
        </div>
        <div class="msg_box readed">
          <h2><span>2018-11-12</span></h2>
          <p>...</p>
          <i class="up"></i>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'msg-list',
    data () {
      return {
        options: {
          pullDownRefresh: {
            threshold: 60,
            txt: ''
          },
          pullUpLoad: {
            threshold: 60,
            txt: {
              more: '',
              noMore: ''
            }
          },
          scrollbar: {
            fade: true
          }
        }
      }
    },
    methods: {
      onPullingDown () {
        // 
        setTimeout(() => {
          this.$refs.scroll.forceUpdate()
        }, 1000)
      },
      onPullingUp () {
        // 
        setTimeout(() => {
          this.$refs.scroll.forceUpdate()
        }, 1000)
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less">
  .msg_list {
    width: 100%;
    height: auto;
    padding: 15px 0 0 0 !important;
    color: #333;
    .msg_box {
      width: 100%;
      height: auto;
      background: #fff;
      position:relative;
      padding: 0 15px 13px 15px;
      i{
         width:30px;
         height:30px;
         display:block;
         background:red;
         position:absolute;
         right:12px;
         bottom:9px;
      }
      i.up{
         background:url("../../static/images/down_icon.png") no-repeat center center;
         background-size:16px 16px;
      }
      i.down{
        background:url("../../static/images/up_icon.png") no-repeat center center;
        background-size:16px 16px;
      }
      h2 {
        font-size: 16px;
        width: 100%;
        border-top:solid 1px #ebebeb;
        padding: 8px 0 3px 0;
        line-height: 33px;
        text-align: left;
        span {
          float: right;
          font-size: 12px;
          color: #999999;
        }
      }
      p {
        font-size: 13px;
        color: #666666;
        line-height: 1.5em;
        padding:0 35px 0 0;
        height:20px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      p.all{
         overflow:visible !important;
         white-space: normal !important;
         height:auto !important;
      }
    }
    .msg_box:first-child{
       h2{
          border:none;
       }
    }
    .msg_box.readed{
       color:#999999;
       p{
         color:#999999;
       }
    }
  }
</style>



